body {
  margin: 0 auto;
  padding: 0;
}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}
@font-face {
  font-family: "Opensans";
  src: url("../fonts/opensans.woff");
  src: url("../fonts/opensans.woff2");
  font-display: swap;
}
@font-face {
  font-family: "Opensans-bold";
  src: url("../fonts/opensansbold.woff");
  src: url("../fonts/opensansbold.woff2");
  font-display: swap;
}
.general-font {
  font-family: "Opensans", "Arial", sans-serif;
  font-size: 15px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 24px;
  letter-spacing: normal;
  color: #444444;
}
.title-font {
  font-family: "Opensans-bold", "Arial", sans-serif;
  font-size: 25px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: 30px;
  letter-spacing: normal;
  color: #000000;
}
