.main-nav__mobile-wrapper {
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  margin-top: 22px;
  position: relative;
  width: 320px;
  border-bottom: 1px solid #ececec;
}
.main-nav__toggle {
  position: absolute;
  top: -2px;
  right: 25px;
  display: block;
  width: 21px;
  height: 20px;
  z-index: 2;
  border: none;
  cursor: pointer;
  background-color: transparent;
  padding-right: 27px;
  padding-bottom: 34px;
}
.main-nav__toggle:hover {
  opacity: 0.6;
}
.main-nav__toggle:active {
  opacity: 0.3;
}
.main-nav__mobile-wrapper--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 12px;
  right: 6px;
  width: 20px;
  height: 14px;
  background-image: url("../img/icon-menu-open.svg");
  background-repeat: no-repeat;
}
.main-nav__mobile-wrapper--opened {
  border-bottom: 0;
}
.main-nav__mobile-wrapper--opened .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 10px;
  right: 5px;
  width: 21px;
  height: 20px;
  background-image: url("../img/icon-menu-close.svg");
  background-repeat: no-repeat;
}
.main-nav__mobile-wrapper--no-js .main-nav__toggle {
  display: none;
}
.main-nav__list {
  margin: 0;
  padding: 0;
}
.site-list {
  border: 1px solid #ececec;
}
.site-list__item {
  list-style: none;
}
.site-list__item:first-of-type {
  border-bottom: 1px solid #ececec;
}
.site-list__item-link {
