  line-height: 24px;
  display: block;
  padding-bottom: 28px;
  text-align: center;
  padding-top: 25px;
}
.site-list__item-link:hover {
  opacity: 0.6;
}
.site-list__item-link:active {
  opacity: 0.3;
}
.additional-list__item-link {
  text-decoration: none;
  padding: 24px 50px;
}
.additional-list__item-link:hover {
  opacity: 0.6;
}
.additional-list__item-link:active {
  opacity: 0.3;
}
.additional-list__item {
  display: none;
}
.user-list__item {
  list-style: none;
  border: 1px solid #ececec;
  border-top: none;
  position: relative;
}
.user-list__item-link {
  font-family: "Opensans-bold", "Arial", sans-serif;
  font-size: 17px;
  font-weight: bold;
  font-style: normal;
