.modal {
  position: fixed;
  left: calc(50% - 160px);
  top: calc(50% - 168px);
  width: 320px;
  min-height: 336px;
  box-shadow: 0 5px 10px 0 rgba(0, 1, 1, 0.25);
  border: solid 1px #c6c6c6;
  margin: 0 auto;
  background-color: white;
  z-index: 10;
  display: none;
}
.modal-show {
  display: block;
}
.modal__title {
  font-family: "Opensans-bold", "Arial", sans-serif;
  font-size: 25px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: 30px;
  letter-spacing: normal;
  color: #000000;
  margin: 0;
  text-align: center;
  text-transform: uppercase;
  padding: 32px 82px 0 82px;
  margin-bottom: 10px;
}
@media (min-width: 768px) {
  .modal__title {
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .modal__title {
    font-size: 53px;
    line-height: 57px;
  }
}
.modal__description {
  font-family: "Opensans", "Arial", sans-serif;
  font-size: 15px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 24px;
  letter-spacing: normal;
  color: #444444;
  padding: 0;
  margin: 0;
  line-height: 30px;
  text-align: center;
  margin-bottom: 24px;
}
.modal__buttons-wrapper {
  padding-left: 27px;
  padding-right: 27px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 32px;
}
.modal__size {
  position: absolute;
  z-index: -1;
  opacity: 0;
}
.modal__size-label {
  font-family: "Opensans-bold", "Arial", sans-serif;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: 20px;
  letter-spacing: normal;
  color: #444444;
  font-size: 17px;
  line-height: 17px;
  padding: 14px 33px 18px 37px;
}
.modal__size + .modal__size-label {
  position: relative;
  cursor: pointer;
  outline: none;
  border: 2px solid rgba(68, 68, 68, 0.1);
}
.modal__size + .modal__size-label:hover {
  border-color: rgba(68, 68, 68, 0.3);
}
.modal__size + .modal__size-label:active {
  border-color: #63d1bb;
}
.modal__add {
  font-family: "Opensans-bold", "Arial", sans-serif;
  font-size: 17px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: 20px;
  letter-spacing: normal;
  color: #444444;
  text-align: center;
  text-transform: uppercase;
  padding: 14px 79px 16px 82px;
  margin-left: 30px;
  background-color: #63d1bb;
  color: white;
  border: none;
}
.modal__add:hover {
  background-color: #56c3b2;
}
.modal__add:active {
  opacity: 0.3;
}
